.nav {
  &__list {
    display: none;
    list-style: none;
    justify-content: space-around;
    align-items: center;
    padding-left: 0;

    @media (min-width: 700px) {
      display: flex;
    }
  }

  &__top-level1 {
    position: relative;

    &:hover {
      .nav__sub-list {
        display: block;
      }
    }

    p {
      display: none;

      @media (min-width: 700px) {
        display: block;
      }
    }
  }

  &__sub-list {
    display: block;
    list-style: none;

    @media (min-width: 700px) {
      display: none;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      list-style: circle inside;
      padding: 20px 10px 20px 20px;
      border: solid #b8d9a5 1px;
      width: 150px;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      background-color: #05065a;
      text-align: left;
    }

    li {
      margin: 8px 0 0 !important;

      &:last-child {
        margin: 16px 0 0 !important;
      }
     
      @media (min-width: 700px) {
        margin: 0 0 10px !important;

        &:last-child {
          margin: 0 !important;
        }
      }
    }
  }

  &.is-open {
    width: 100%;
    display: flex;

    .nav__list {
      display: flex;
      transition: none;
      position: fixed;
      top: 110px;
      left: 0;
      flex-direction: column;
      align-items: center;
      z-index: 20;
      width: 100%;

      .nav__li {
        margin: 8px 0;
      }

      .nav__item {
        font-size: 40px;
      }
    }
  }

  &.is-hidden {
    @media (max-width: 699px) {
      display: none;
    }
  }
}
