.container {
  position: relative;
  z-index: 5;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  max-width: 1200px;
  border: solid 1px #05065a;
  margin: 0 auto;
  padding: 0 20px;
  color: #b8d9a5;

  .subheading {
    font-size: 30px;

    @media (min-width: 600px) {
      font-size: 60px;
    }
  }

  &__left-photo {
    display: none;

    @media (min-width: 1550px) {
      display: block;
      position: fixed;
      top: 60%;
      left: 0;
      width: 200px;
      height: auto;
    }
  }

  &__right-photo {
    display: none;

    @media (min-width: 1550px) {
      display: block;
      position: fixed;
      top: 10%;
      right: 0;
      width: 200px;
      height: auto;
    }
  }
}

.hideOnMobile {
  display: none;

  @media (min-width: 600px) {
    display: inline-block;
  }
}

main {
  min-height: calc(100vh - 549px);
  margin: 50px 0;

  @media (min-width: 600px) {
    min-height: calc(100vh - 657px);
    margin: 100px 0;
  }
}
