* {
  margin: 0;
  padding: 0;
}

.app {
  text-align: center;
  background: #05065a;
  font-family: "Dancing Script", cursive;
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
}

.hide {
  display: none;
}

.clearfix {
  overflow: auto;

  &::after {
    content: "";
    clear: both;
    display: table;
  }
}

