.footer {
  padding-bottom: 130px;

  &__heading {
    position: relative;
    z-index: 5;
    border-bottom: 3px solid #b8d9a5;
    width: 150px;
    display: inline-block;
    padding-bottom: 10px;
    margin: 10px 0 20px;
  }

  &__date {
    position: relative;
    z-index: 5;
    font-family: "Montserrat", sans-serif;
  }

  &__flower {
    position: absolute;
    bottom: 5px;
    right: 10px;
    width: 350px;
    height: auto;
    transform: rotate(170deg);
  }
}
